import logo from './logo.svg';
import './App.css';

import React, {useEffect} from 'react';
import { useDispatch, useSelector } from 'react-redux'
import { BrowserRouter as Router, Switch, Route, Link, NavLink } from "react-router-dom"

import {initializeItems} from './reducers/ItemReducer'
import {logout} from './reducers/LoginReducer'

import itemService from './services/items';

import Item from './components/Item';
import ItemList from './components/ItemList';
import ItemForm from './components/ItemForm';

import LoginForm from './components/LoginForm'

const App = () => {

  const dispatch = useDispatch()
  useEffect(() => {
    itemService
      .getAll().then(items => dispatch(initializeItems(items)))
  }, [dispatch])


  const items = useSelector(state => state.items)
  const user = useSelector(state => state.loggedUser)

  const itemById = (id) =>
    (items.find(i => i.id === id))

  const LoginStatus = () => {
    const logoutButton = {display: user === null ? 'none' : '', backgroundColor: "darkGrey", color:"black", padding:5, borderRadius:5, cursor: "pointer",float:"right", marginRight:"25px"}
    return (
      <div>
        <p>
          <a style={{float:"left", marginLeft:"25px"}}>Logged in as {user.name}</a>
          <a style={logoutButton} onClick={handleLogout}>logout</a>
        </p>
        <br/>
      </div>
    )
  }

  const handleLogout = async(e) => {
    await itemService.setToken(null)
    await localStorage.removeItem('loggedUser')
    dispatch(logout())

  }

  return (
    <div className="App">
      <Router>
        <header className="App-header">
          <Link exact to="/"><img src={logo} className="App-logo" alt="logo" /></Link>
        </header>
        <div>
        {user !== null && <LoginStatus/>}
        {(user === undefined || user === null) && <LoginForm/>}
        <Route exact path="/" render={() => <ItemList/>}/>
        <Route exact path="/items/:id" render={({match}) => <Item key={match.params.id} item={itemById(match.params.id)} />}/>
        {user !== null && <ItemForm/>}
      </div>
      </Router>
    </div>
  )
}

export default App
