import '../index.css'

import React, {useState} from 'react'
import { useDispatch } from 'react-redux'
import { createItem } from '../reducers/ItemReducer'
import itemService from '../services/items'


const ItemForm = (props) => {

  const dispatch = useDispatch()

  const [showForm, setShowForm] = useState(false)
  const [selectedUnits, setSelectedUnits] = useState('')
  const [commonUsecases, setCommonUsecases] = useState([])
  const [newCommonUsecase, setNewCommonUsecase] = useState('')

  const handleShowMore = () => {
    setShowForm(!showForm)
  }

  function validateUsecase(usecase) {
    let error=''
    const numericUsecase = parseInt(usecase)

    if (!parseInt(numericUsecase) & parseInt(numericUsecase)!==0) {
      error = "only numbers!"
    } else if (commonUsecases.indexOf(numericUsecase)>=0) {
      error="You have already added this usecase!"
    } else if (numericUsecase <= 0) {
      error = "Only positive values to usecases"
    } else {
      error='no errors'
    }
    return error
  }

  const addCommonUsecase = (event) => {
    const validation=validateUsecase(newCommonUsecase)
    if (validation==='no errors') {
        setCommonUsecases(commonUsecases.concat(parseInt(newCommonUsecase)))
        setNewCommonUsecase('')
      } else {
        console.log(`${validation}`)
        setNewCommonUsecase('')
    }
  }

  const clearCommonUsecases = () => {
    setCommonUsecases([])
  }

  const addNewItem = async (event) => {
      try{
        event.preventDefault()
        const newItem = {
          name: event.target.name.value,
          units: event.target.units.value,
          use: [],
          common_usecases: commonUsecases
        }
        itemService.create(newItem).then(createdItem=>dispatch(createItem(createdItem)))

        event.target.name.value = ''
        event.target.units.value = ''
        setNewCommonUsecase('')
        setCommonUsecases([])

      } catch (ex) {
          const error = `couldn't add new item due to an error: ${ex}`
          alert(error)
        }
      }

  const showMore = { display: showForm ? '' : 'none' }
  const displayCommonUsecases = { display: commonUsecases.length === 0 ? 'none' : '' }
  const mockButton = {display: newCommonUsecase === '' ? 'none' : '', backgroundColor: "coral", padding:5, borderRadius:5, cursor: "pointer"}
  const clearButton = {display: commonUsecases.length > 0 ? '' : 'none', backgroundColor: "teal", color:"white", padding:5, borderRadius:5, cursor: "pointer"}

  return(
    <div>
    <div onClick={handleShowMore} style={{cursor: "pointer"}}>
      <h3>Add new item</h3>
    </div>
    <div style={showMore}>
      <form onSubmit={addNewItem}>
        name: &emsp;
        <input
          type="text"
          name="name"
        />
        <br/><br/>
        What do you measure?
        <br/>
        <input onChange={({ target }) => setSelectedUnits(target.value)} type="radio" id="km" name="units" value="km"/>
        distance &nbsp;
        <input onChange={({ target }) => setSelectedUnits(target.value)} type="radio" id="h" name="units" value="h"/>
        time
        <br/><br/>
        Add common usecases for the item
        <br/>
        <input onChange={({ target }) => setNewCommonUsecase(target.value)} type="number" value={newCommonUsecase}/>
        <p><a style={mockButton} onClick={addCommonUsecase}>Add</a></p>

        <p style={displayCommonUsecases}>[{commonUsecases.map(u=>
          <a> {u}{selectedUnits} </a>
        )}] &emsp; <a style={clearButton} onClick={clearCommonUsecases}> clear </a> </p>
        <br/><br/>
        <button>Add a new item</button>
        <br/><br/>
        </form>
      </div>
      </div>
  )
}



export default ItemForm
