import React, {useState} from 'react'
import { useDispatch } from 'react-redux'

import {login} from './../reducers/LoginReducer'
import loginService from '../services/login'
import itemService from '../services/items'

const LoginForm = (props) => {
  const dispatch = useDispatch()

  const [showForm, setShowForm] = useState(false)

  const handleShowMore = () => {
    setShowForm(!showForm)
  }

  const showMore = { display: showForm ? '' : 'none' }

  const handleLogin = async(e) => {
      e.preventDefault()
      try{
        const user = await loginService.login({
          username: e.target.username.value,
          password: e.target.password.value
        })
        window.localStorage.setItem('loggedUser', JSON.stringify(user))
        await itemService.setToken(user.token)

        const notification = `Succesfully logged in as ${user.name}`
        dispatch(login(user))
        console.log(notification)

      } catch(exception) {
          const error = 'username or password was incorrect'
          console.log(error)
        }
    }

  return (
      <div>
        <h2>Log in</h2>
        <form onSubmit={handleLogin}>
          <div>
            username &nbsp;
            <input
              type="text"
              name="username"
            />
          </div>
          <div>
            password &nbsp;
            <input
              type="password"
              name="password"
              />
          </div>
          <button style={{marginTop:"5px"}}>log in</button>
        </form>
      </div>
    )
}

